#contact {
  background-color: #ce8633;
  background-image: -webkit-gradient(linear, left top, right top, from(#e6c569), to(#ce8633));
  background-image: -webkit-linear-gradient(left, #e6c569, #ce8633);
  background-image: -o-linear-gradient(left, #e6c569, #ce8633);
  background-image: linear-gradient(to right, #e6c569, #ce8633);
}

#contact a {
  color: black;
  text-decoration: none;
  vertical-align: middle;
}


#contact a:hover {
  color: white;
  text-decoration: none;
}

#contact span {
  font-size: 1.15rem;
}

.google-maps {
  position: relative;
  padding-bottom: 75%;
  height: 0;
  overflow: hidden;
}
.google-maps iframe {
  position: absolute;
  top: 30px;
  left: 0;
  width: 100% !important;
  height: 100% !important;
  max-height: 300px;
}

.contact-text {
  font-size: 1.5rem;
  text-align: left;
  margin: 0 20px 0 20px; 
}

#contact {
  text-align: left;
 }

.map-header {
  text-align: center;
}

.fa-envelope, .fa-phone, .fa-github, .fa-linkedin-in, .fa-globe-americas {
  font-size: 120%;
}

.google-maps  {
  margin: 20px 20px 0 20px; 
  /* float: right; */
  -ms-flex-pack: right;
      justify-content: right;
}

@media (max-width: 900px) {
  .contact-text, .fa-envelope, .fa-phone, .fa-github, .fa-linkedin-in, .fa-globe-americas {
    font-size: 90%;
    padding-top: 1rem; 
  }
  #contact {
    padding-top: 1.5em;
  }
}
@media (max-width: 750px) {
  .contact-text, .fa-envelope, .fa-phone, .fa-github, .fa-linkedin-in, .fa-globe-americas {
    font-size: 85%;
  }
}
@media (max-width: 600px) {
  .contact-text, .fa-envelope, .fa-phone, .fa-github, .fa-linkedin-in, .fa-globe-americas {
    font-size: 90%;
  }
  .google-maps{
    display: none;
  }
}


