#about {
  background-color: #ce8633;
  background-image: -webkit-gradient(linear, left top, right top, from(#e6c569), to(#ce8633));
  background-image: -webkit-linear-gradient(left, #e6c569, #ce8633);
  background-image: -o-linear-gradient(left, #e6c569, #ce8633);
  background-image: linear-gradient(to right, #e6c569, #ce8633);
  padding: 15px;
}
.about-img-container img {
  height: 160px;
  width: 160px;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 20% 33%;
     object-position: 20% 33%;
  border-radius: 50%;
}

.about-img-container img:hover {
  cursor: auto;
  width: 160px;
  height: 160px;
  border: none;
  border-radius: 50%;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}

.about-img-container {
  float: left;
  margin: 0 20px 0 20px ;
}

.about-content {
  text-align: center;
}

.about-text-items {
  text-align: left;
}

#about-name-location {
  margin: 20px 0 0 0 ;
}

